*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  background: radial-gradient(circle closest-corner at 160px 80px, #3a250f, #121212);
  min-height: 100vh;
}

.App-swipe{
  width: 100%;
}

.App-swiper__img{
  max-width: 750px;
  height: 750px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
 }

main{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main-title:after{
  content: "";
  display: block;
  height: 3px;
  background: linear-gradient(96deg, rgba(164,255,0,1) 0%, rgba(121,107,9,1) 50%, rgba(157,1,1,1) 96%);
  margin-top: 5px;
}

.App-content{
 margin-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-content__angle{
  align-self: flex-start;
}

.App-content__frequency{
  align-self: flex-end;
}

.App-content__title{
  font-size: 16px;
  letter-spacing: 5px;
  color: #FFF;
  line-height: 1.3;
  justify-content: flex-start;
  text-align: left;
  text-shadow: #FFF 0px 0px 5px, #FFF 0px 0px 10px, #FFF 0px 0px 15px,  #9c8b36 0px 0px 20px,  #9c8b36 0px 0px 30px,  #9c8b36 0px 0px 40px,  #9c8b36 0px 0px 50px,  #9c8b36 0px 0px 75px, 20px 18px 15px  #9c8b36;
  text-transform: uppercase ;
}

.App-content__desc{
  margin-top: 16px;
  font-size: 16px;
  line-height: 1.75;
  color: #FFF;
  width: 500px;
  text-align: left;
}

.App-content__img{
  max-width: 950px;
}



